.App-title {
  margin: 10px 0 10px 0;
}

.btn-group-justified, .btn-group {
  display: -ms-flexbox;
  display: flex;
  background-color: rgb(82, 83, 96);
  width: 100%;
}

.btn-group-justified .btn, .btn-group-justified .btn-group {
  background-color: rgb(82, 83, 96);
  color: white;
  padding-top: 10px;
  padding-bottom: 10px;
  border: none;
}
.App-logo-m {
  display: none;
}


.btn-group-justified .btn {
  width: 100%;
}

.btn-group-justified .dropdown-menu {
  left: auto;
}

.btn-primary:hover, .btn-success:active, .btn-success:focus, .btn-primary:focus, .btn-primary:active, .btn-primary.active, .open>.dropdown-toggle.btn-primary {
    background-color: rgb(50, 50, 50) !important;
    color: rgb(255, 107, 0);
    border: none !important;
    -webkit-box-sizing: content-box !important;
            box-sizing: content-box !important;
    -webkit-box-shadow: none !important;
            box-shadow: none !important;
}

@media screen and (max-width: 600px) {
  .App-header {
    height: auto;
    margin-bottom: 1em;
    padding-top: 1em;
  }
  .App-title {
    position: relative;
    display: inline;
    margin-left: 1em;
    margin-right: 1em;
    font-size: 1.15em;
    width: 80%;
  }

  .App-logo-m {
    position: relative;
    display: inline;
    height: auto;
    width: 10%;
  }

  .btn-group-justified, .btn-group {
    padding-bottom: 0;
  }

  .App-logo {
    display: none;
  }

  .btn-group-justified a, .btn-group a {
    padding-top: 2%;
  }

  .btn-group-justified .btn, .btn-group-justified .btn-group  {
    border-color: rgb(82, 83, 96) !important;
    border-radius: 2px !important;
    border-style: solid !important;
    border-width: medium !important;
  }
}



