.footer-root {
  position: relative;
  margin-top: 7em;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: none;
  font-weight: 300;
  color: #202020;
  font-size: 15px;
  line-height: 24px;
  background: #2D2D2D;
  -webkit-box-shadow: inset 0 10px 10px -5px #0d1116;
          box-shadow: inset 0 10px 10px -5px #0d1116;
  padding-top: 2em;
  padding-bottom: 2em;
  -webkit-font-smoothing: antialiased;
}

.footer-sitemap {
  display: flex;
  display: -ms-flexbox;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0 auto 3em;
}

.footer-sitemap > div {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 0 auto;
  padding: 20px 15px;
  text-align: center;
}

.footer-sitemap a {
  color: white;
  text-decoration: none;
  display: block;
}

.footer-sitemap h5 {
  font-family: inherit;
  font-size: 14px;
  font-weight: bold;
  line-height: 20px;
  color: white;
}

.footer-sitemap a:hover {
  color: rgb(255, 107, 0);
}

.footer-copyright {
  color: rgba(255,255,255,0.4);
  text-align: center;
}

.footer-logo {
  display: block;
  margin: 1em auto;
  opacity: 0.4;
  -webkit-transition: opacity 0.15s ease-in-out;
  -o-transition: opacity 0.15s ease-in-out;
  transition: opacity 0.15s ease-in-out;
}

.footer-logo:hover {
  opacity: 1;
}

@media screen and (max-width: 600px) {
  .footer-sitemap {
    display: none !important;
  }

  .footer-root {
    margin-top:5em;
  }
}



