.app-body {
  width: 60%;
  margin: auto;
}


.app-content {
  width: 100%;
  margin: auto;
}

.curriculum_container {
  position: relative;
  display: block;
  margin-left: 4em;
  margin-right: 4em;
}

.curriculum_container:first-child {
  font-size: 1em;
  padding-top: 1em;
  margin-top: 1em;
}



.app-content p {
  float: left;
  margin-right: 5%;
  margin-left: 5%;
  width: 40%;
  font-size: 1.15em;
  margin-bottom: 5em;
}


.back-btn {
  position: relative;
  float: right;
}

.back-btn a {
  padding: 0.25em 1em 0.25em 1em;
  background-color: rgb(82, 83, 96);
  color: white;
}

.back-btn a:hover {
  background-color: rgb(50, 50, 50);
  color: white;
}

@media screen and (max-width: 1000px) {
  .app-body {
    width: 90%;
    margin: auto auto 15em auto;
  }

  .curriculum_container {
    margin-left: 1em;
    margin-right: 1em;
  }

  .back-btn a {
    padding: 0.5em 1em 0.5em 1em;
  }

  .app-content p {
    float: left;
    margin-right: 2%;
    margin-left: 2%;
    width: 46%;
    font-size: 1.15em;
  }

}



