.welcome-body {
  background-color: inherit;
}

.content {
  position: relative;
  margin-left: 1em;
  margin-right: 1em;
  font-size: 1em;
}

.center {
  float: none !important;
  width: auto;
}

@media screen and (max-width: 600px) {
  .content {
    margin-left: 1.2em;
    margin-right: 1.2em;
  }
  .subHeading {
    margin-left: 1em;
    margin-right: 1em;
  }
}



