.card-body {
    position: relative;
    display: inline-block;
    background-color: white;
    width: 15em;
    margin: 0em 1em 0 0;
}

.card-body img {
  height: 15em;
  width: auto;
}

.card-body:hover {
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card-body:hover h3 {
  color: rgb(6, 69, 173);
}

.card-body h3 {
  text-align: center;
  margin: 0;
  width: inherit;
  margin-bottom: 2em;
  overflow: hidden;
  color: rgb(6, 69, 173, 0.7);
}

@media screen and (max-width: 600px) {
  .card-body {
    display: block;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
  }
  .card-body img {
    height: 10em;
    width: auto;
  }
  .card-body h3 {
    font-size: 1em;
    width: auto;
    padding-bottom: 1em;
    margin-right: auto;
    margin-left: auto;
  }
}



