.about-body {
  position: relative;
  margin-left: 20em;
  margin-right: 20em;
}

.steps {
  position: relative;
  text-align: left;
}

.imgBox {
  position: relative;
  overflow: hidden;
  display: block;
  width: 140px;
  height: 300px;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
}

.img {
  position: relative;
  width: 450px;
  left: -155px;
}



@media screen and (max-width: 600px) {
  .about-body {
    margin-right: 1em;
    margin-left: 1em;
    padding: 0;
  }

  .imgBox {
    padding: 0;
    margin: 0;
  }

  .steps {
    position: relative;
    list-style: none;
  }
}



