.module_list_container {
  position: relative;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.module_list_col {
  position: relative;
  display: block;
  width: 50%;
  overflow: hidden;
  margin-top: 0.75em;
  margin-bottom: 0.75em;
  margin-right: auto;
  margin-left: auto;
  padding: 0;
}

.module_list_col img {
  display: -ms-flexbox;
  display: flex;
  float: left;
  height: 10em;
}

.module_list_col div {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  background-color: rgb(255, 255, 255);
  margin: auto;
  height:10em;
  color: rgba(6, 69, 173, 0.7);
}

.module_list_col div h3 {
  position: relative;
  font-size: 1.15em;
  margin: auto auto auto auto;
}

.module_list_col:hover {
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.module_list_col:hover div h3 {
  color: rgb(6, 69, 173);
}


@media screen and (max-width: 1000px) {
    .module_list_container {
        width: 90%;
        margin: auto;
        margin-bottom: 2em;
        text-align: center;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-item-align: center;
            align-self: center;
    }

    .module_list_col:hover {
      -webkit-transition: 0.25s;
      -o-transition: 0.25s;
      transition: 0.25s;
      -webkit-box-shadow: none;
              box-shadow: none;
    }

    .module_list_col:hover div h3 {
      color: rgba(6, 69, 173, 1.0);
    }

    .module_list_col {
      width: 100%;
      font-size: 1em;
      margin-top: 0.25em;
      margin-bottom: 0.25em;
      margin-left: auto;
      margin-right: auto;
    }

    .module_list_col img {
      display: -ms-flexbox;
      display: flex;
      float: left;
      width: auto;
      height: 4.5em;
    }

    .module_list_col div {
      height: 4.5em;

    }

    .module_list_col div h3 {
      font-size: 1em;
    }


}



