html, body {
    max-width: 100%;
    overflow-x: hidden;
}

.App {
  text-align: center;
  background: rgb(238, 238, 238);
}

.App-logo {
  height: 100px;
  margin: 10px;
}

.App-header {
  background-color: rgb(82, 83, 96);
  height: 180px;
  color: white;
  margin-bottom: 70px;
}

.App-title {
  font-size: 1.5em;
  margin: 25px 0 0px 0;
}

.App-intro {
  font-size: large;
}

.App-intro-body {
  float: left;
  margin-left: 1em;
  margin-top: 1em;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/* Curriculum */

.curriculum_container {
    text-align: left;
    margin-left: 10px;
    background: rgb(238, 238, 238);
}



